import styles from './charge.module.css';

const Charge=()=>{
    return(
        <>
        <div className={styles.a}><p className={styles.chargepoint}>  : 12,000</p></div>
        <div className={styles.box1}>
            <p className={styles.text1}> </p>
            <input type='text' className={styles.entermoney} placeholder='14,000'></input>
        </div>
        <div className={styles.b}><p className={styles.afterpoint}>     =   22,000</p></div>
        <div className={styles.c}><button className={styles.addbank}>-  </button></div>
        <div className={styles.box2}>
            <p className={styles.text2}> </p>
            <input type='text' className={styles.enterbankname} placeholder=''></input>
        </div>
        <div className={styles.footer}>
            <button className={styles.chargebtn}></button>
        </div>
        </>
    );
}
export default Charge;