import { useState } from 'react';
import styles from './account.module.css';
import { FaEye, FaEyeSlash } from 'react-icons/fa';
import { TextField, Button, FormControl, FormHelperText } from '@mui/material';

const Account=()=>{
    const [isChanging,setIsChanging]=useState(false);

    const changepwHandler=()=>{
        setIsChanging(true);
    };

    const confirmpwHandler=()=>{
        setIsChanging(false);
    };

    const [showPassword,setShowPassword]=useState(false);

    const toggleShowPassword=()=>{
        setShowPassword((prevShowPassword)=>!prevShowPassword);
    };

    let isConnected=false;
    function toggleConnection(){
        const toggleButton=document.getElementById("toggleButton");
       

        if(isConnected){
            isConnected=false;
            toggleButton.innerText="";
        }
        else{
            isConnected=true;
            toggleButton.innerText="";
        }
    };

    //     errortext1 
    //    errortext2
    // error    text  

    const [currentPassword, setCurrentPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [passwordError, setPasswordError] = useState('');

    const handleChangePassword = () => {
        setPasswordError('');
      
        if (newPassword !== confirmPassword) {
          setPasswordError('errortext2');
          return;
        }
      
        if (currentPassword === newPassword) {
          setPasswordError('errortext1');
          return;
        }
    };


    return(
    <>
    <div className={styles.bigbox}>
        <div className={styles.middlebox1}>
            <p className={styles.headtext}> </p>
            <div className={styles.content1}>
                <div className={styles.smallbox1}>
                    <div className={styles.boxwrap}>
                    <img className={styles.snslogo}></img>
                    <p className={styles.snsname}></p>
                    </div>
                    <div className={styles.btnwrap}>
                    <button className={styles.connectbtn} id='toggleButton' onClick={toggleConnection}></button>
                    
                    </div>
                </div>
                
                <div className={styles.smallbox1}>
                <div className={styles.boxwrap}>
                    <img className={styles.snslogo}></img>
                    <p className={styles.snsname}>Naver</p>
                </div>
                <div className={styles.btnwrap}>
                    <button className={styles.connectbtn}></button>
                    </div>   
                </div>
            </div>
        </div>
        <div className={styles.middlebox2}> 
            <div className={styles.content2}>
                {isChanging?(
                    <>
                    <div className={styles.boxing0}>
           <div className={styles.boxing1}>
                    <p className={styles.pwtext}> </p><br/>
                    <div className={styles.inputdiv}>
                    <input type={showPassword?'text':'password'} className={styles.inputbox}  />
                    {showPassword?(<FaEyeSlash className={styles.eyeIcon} onClick={toggleShowPassword} />):(
                        <FaEye className={styles.eyeIcon} onClick={toggleShowPassword} />
                    )}
                    </div>
                    {passwordError === 'errortext1' && (
                     <p className={styles.errortext}>  .    .</p>)}
                </div>
                <div className={styles.boxing2}>
                    <p className={styles.pwtext}>  </p><br/>
                    <div className={styles.inputdiv}>
                    <input type={showPassword?'text':'password'} className={styles.inputbox} />
                    {showPassword?(<FaEyeSlash className={styles.eyeIcon} onClick={toggleShowPassword} />):(
                        <FaEye className={styles.eyeIcon} onClick={toggleShowPassword} />
                    )}
                    </div>
                    {passwordError === 'errortext2' && (
                        <p className={styles.errortext}>    .</p>
                    )}
                </div>
                </div>
                <div className={styles.changepw}><button className={styles.changepwbtn} onClick={confirmpwHandler}></button></div>
                    </>
                ):(
                    <>
                    <div className={styles.smallbox2}>
                    <p className={styles.pwtext}>  </p>
                    <div className={styles.pwbox}>~~~</div>
                </div>
                <div className={styles.changepw}><button className={styles.changepwbtn} onClick={changepwHandler}></button></div>
                    </>
                )}
        </div>
        </div>
    </div>
    </>
    );
}
export default Account;