import React, { useState } from 'react';
import Login from '../LoginPage/Login.JPG';
import styles from './MembershipPage.module.css';
import { useForm } from 'react-hook-form';
import { useRef } from 'react';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
function CreateAccountPage() {
  //
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const re_pwd = useRef();
  re_pwd.current = watch('re_pwd');
  console.log(watch('email'));
  const onSubmit = (data) => {
    console.log('data', data);
  };
  //
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [idValue, setIdValue] = useState('');
  const [pwValue, setPwValue] = useState('');
  const [again_pwValue, setAgain_pwValue] = useState('');
  const [nameValue, setNameValue] = useState('');
  const [emailValue, setEmailValue] = useState('');
  //const [ischeckedValue, setIsCheckedValue] = useState(false);
  //const [replace] = useHistory;
  const handleIdChange = (event) => {
    setIdValue(event.target.value);
  };
  const handlePwChange = (e) => {
    setPwValue(e.target.value);
  };
  const handleAgain_pwChange = (e) => {
    setAgain_pwValue(e.target.value);
  };
  const handleNameChange = (e) => {
    setNameValue(e.target.value);
  };
  const handleEmailChange = (e) => {
    setEmailValue(e.target.value);
  };
  /*const handleCheckedChange = (e) => {
  setIsCheckedValue(!ischeckedValue);
  };*/

  const onSigninHandler = async () => {
    const body = {
      user_id: idValue,
      password: pwValue,
      again_password: again_pwValue,
      name: nameValue,
      email: emailValue,
      //purpose: ischeckedValue,
    };
    try {
      const response = await axios.post('http://photolancer.shop/api/v1/users/join', body);
      console.log('', response.data.jwt);
      alert(' !!');

      navigate('/');
    } catch (error) {
      console.log('Error:', error.message);
      alert(' ');
    }
    //localStorage.setItem('token', response.data.jwt);
    //replace('/');
  };

  return (
    <div className={styles.MembershipPage}>
      <img className={styles.L_img} src={Login} alt="login_camera" />
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className={styles.create_account2}>
          <p>Create Account</p>
          <div className={styles.create_input}>
            <label></label>
            <div>
              <input
                className={styles.input_user}
                label="id"
                name="id"
                margin="normal"
                autoFocus
                placeholder=" "
                {...register('id', {
                  required: true,
                  pattern: /^[A-z]{8,20}$/,
                })}
                value={idValue}
                onChange={handleIdChange}
              ></input>
              {errors.id && (
                <span className={styles.account_error1}>
                  <span className={styles.account_error2}></span> .
                </span>
              )}
            </div>
          </div>
          <div className={styles.create_input}>
            <label></label>
            <div>
              <input
                className={styles.input_user}
                label="email"
                name="email"
                margin="normal"
                autoFocus
                placeholder=" "
                {...register('email', { required: true, pattern: /^\S+@\S+$/i })}
                value={emailValue}
                onChange={handleEmailChange}
              ></input>
              {errors.email && (
                <span className={styles.account_error1}>
                    <span className={styles.account_error2}></span> .
                </span>
              )}
            </div>
          </div>
          <div className={styles.create_input}>
            <label></label>
            <div>
              <input
                className={styles.input_user}
                label="pwd"
                name="pwd"
                margin="normal"
                autoFocus
                placeholder=" "
                {...register('pwd', {
                  required: true,
                  pattern: /^(?=.*[!@#$%^&*])(?=.*[0-9])(?=.*[A-z]).{4,}$/,
                })}
                value={pwValue}
                onChange={handlePwChange}
              ></input>
              {errors.pwd && (
                <span className={styles.account_error1}>
                  <span className={styles.account_error2}></span> , ,  .
                </span>
              )}
            </div>
          </div>
          <div className={styles.create_input}>
            <label> </label>
            <div>
              <input
                className={styles.input_user}
                label="re_pwd"
                name="re_pwd"
                margin="normal"
                autoFocus
                placeholder=" "
                {...register('re_pwd', { required: true, validate: (value) => value === re_pwd.current })}
                value={again_pwValue}
                onChange={handleAgain_pwChange}
              ></input>
              {errors.re_pwd && (
                <span className={styles.account_error1}>
                  <span className={styles.account_error2}></span>
                  <span className={styles.account_error2}>  </span>  .
                </span>
              )}
            </div>
          </div>
          <div className={styles.create_input}>
            <label></label>
            <div>
              <input
                className={styles.input_user}
                label="name"
                name="name"
                margin="normal"
                autoFocus
                placeholder=" "
                {...register('name', { required: true })}
                value={nameValue}
                onChange={handleNameChange}
              ></input>
              {errors.name && errors.name.type === 'required' && (
                <span className={styles.account_error1}>
                    <span className={styles.account_error2}></span> .
                </span>
              )}
            </div>
          </div>
          <div className={styles.service_agree}>
            <div className={styles.agree1}> </div>
            <div className={styles.agree2}>
              <label>
                   
                <input
                  type="checkbox"
                  name="purpose"

                  //onChange={handleCheckedChange}
                  //checked={ischeckedValue}
                ></input>
              </label>
            </div>
          </div>
          <button className={styles.signIn} onClick={onSigninHandler}>
            
          </button>
        </div>
      </form>
    </div>
  );
}

export default CreateAccountPage;
